
// ,             

export class PaginationService {
  #currentPage = 0;
  #totalPage = 0;
  #isMobile = false;

  //      .  ,  
  static countFixedPaged = 2;

  constructor(currentPage, totalPage, isMobile) {
    this.#currentPage = currentPage;
    this.#totalPage = totalPage;
    this.#isMobile = isMobile;
  }

  //    
  get() {
    //   
    let totalPage = this.#totalPage;
    //  
    let currentPage = this.#currentPage;
    //    
    let isMobile = this.#isMobile;
    //      ( ).
    //   -    - 2
    //   -    - 4
    let maxPageLeftOrRight =
      PaginationService.countFixedPaged + (isMobile ? 0 : 2); // 2    "..."  " "
    //  
    const startPage = 1;

    let arrayRight;
    let arrayLeft;

    //      ,     "   "     
    if (totalPage - currentPage <= currentPage - startPage) {
      //      
      arrayRight = PaginationService.getArrayRight(
        currentPage,
        totalPage,
        0,
        isMobile
      );
      //   
      let countAdditionalFreePlace = Math.max(
        0,
        maxPageLeftOrRight - arrayRight.length
      );
      //        
      arrayLeft = PaginationService.getArrayLeft(
        currentPage,
        countAdditionalFreePlace,
        isMobile
      );
    } else {
      //      
      arrayLeft = PaginationService.getArrayLeft(currentPage, 0, isMobile);
      //   
      let countAdditionalFreePlace = Math.max(
        0,
        maxPageLeftOrRight - arrayLeft.length
      );
      //        
      arrayRight = PaginationService.getArrayRight(
        currentPage,
        totalPage,
        countAdditionalFreePlace,
        isMobile
      );
    }
    return [
      ...(currentPage == 1 ? [] : ['<=']), //     ,  
      ...arrayLeft, //   
      currentPage, //   
      ...arrayRight, //   
      ...(currentPage == totalPage ? [] : ['=>']), //     ,  
    ];
  }

  //   
  static getArrayRight(
    currentPage,
    totalPage,
    countAdditionalFreePlace,
    isMobile
  ) {
    let pages = [];
    let index;

    // 1.    +   ,   
    for (
      index = currentPage + 1; //       
      index <=
      currentPage +
        PaginationService.countFixedPaged +
        countAdditionalFreePlace; // :   +   (2) +  
      index++
    ) {
      //    >    ,    .  ,    - 
      if (index > totalPage) {
        return [];
      }
      //   
      pages.push(index);

      //     ,   
      if (index == totalPage) {
        return pages;
      }
    }

    // 2.     ,   "..."   
    if (!isMobile) {
      //        ,  "..."  ,     
      if (index == totalPage - 1) {
        pages.push(index);
        pages.push(index + 1);
        //        ,   
      } else if (index == totalPage) {
        pages.push(index);
        //   ...   
      } else {
        pages.push('...');
        pages.push(totalPage);
      }
    }
    return pages;
  }

  //   
  static getArrayLeft(currentPage, countAdditionalFreePlace, isMobile) {
    let pages = [];
    let index;
    const startPage = 1;
    for (
      index = currentPage - 1; //       
      index >=
      currentPage -
        PaginationService.countFixedPaged -
        countAdditionalFreePlace; // :   -   (2) -  
      index--
    ) {
      //   <1,   .  ,    
      if (index < 1) {
        return [];
      }
      //    
      pages.push(index);
      //    1- ,     ,   ,  
      if (index == 1) {
        return pages.reverse();
      }
    }
    if (!isMobile) {
      //        = 2,    2 . 2-  1-
      if (index == 2) {
        pages.push(index);
        pages.push(index - 1);
        //        = 1,    1- 
      } else if (index == 1) {
        pages.push(index);
        //   ...  1 
      } else {
        pages.push('...');
        pages.push(1);
      }
    }

    return pages.reverse();
  }
}


